const entDefaultConfig: IApi = {
  home: '',
  loginUrl: '/plugin-signin/:code?type=rp',
  signupUrl: '/signup',
  apiCC: `/api/v1`,
  apiRP: `/rpapi/v1`,
};

export const envConfig = new Proxy<{ [k: string]: IApi }>(
  {
    oldDev: {
      home: 'http://localhost:3008',
      loginUrl: 'http://192.168.0.152:4004/plugin-signin/:code?type=rp',
      signupUrl: 'http://192.168.0.152:4004/signup',
      apiCC: `http://192.168.0.152:5006/api/v1`,
      apiRP: `/rpapi/v1`,
    },
    dev: {
      home: 'http://localhost:3008',
      loginUrl: 'https://dev.mockdev.cn//plugin-signin/:code?type=rp',
      signupUrl: 'https://dev.mockdev.cn/signup',
      apiCC: `https://dev.mockdev.cn/api/v1`,
      apiRP: `/rpapi/v1`,
    },
    test: {
      home: 'https://dev-user.mockdev.cn/',
      loginUrl: 'https://dev.mockdev.cn/plugin-signin/:code?type=rp',
      signupUrl: 'https://dev.mockdev.cn/signup',
      apiCC: `https://dev.mockdev.cn/api/v1`,
      apiRP: `/rpapi/v1`,
    },
    demo: {
      home: 'https://demorp.mockplus.cn/',
      loginUrl: 'https://user.mockplus.cn/plugin-signin/:code?type=rp',
      signupUrl: 'https://user.mockplus.cn/signup',
      apiCC: `https://app.mockplus.cn/api/v1`,
      apiRP: `/rpapi/v1`,
    },
    prod: {
      home: 'https://rp.mockplus.cn/',
      loginUrl: 'https://user.mockplus.cn/plugin-signin/:code?type=rp',
      signupUrl: 'https://user.mockplus.cn/signup',
      apiCC: `https://app.mockplus.cn/api/v1`,
      apiRP: `/rpapi/v1`,
    },
  },
  {
    get(target, name: string) {
      const webEnvConfig: IApi = (window.MRPBridge?.getWebStore('webEnvConfig') as IApi) ?? {};
      const entConfig: Partial<IApi> = {};

      let origin = window.MRPBridge?.getWebStore('origin') as string;
      if (origin) {
        Object.keys(entDefaultConfig).forEach((k) => {
          if (origin.endsWith('/')) {
            origin = origin.substr(0, origin.length - 1);
          }
          if (origin) {
            entConfig[k as keyof IApi] = origin + entDefaultConfig[k as keyof IApi];
          }
        });
      }

      return {
        ...(target[name] || defaultConfig),
        ...webEnvConfig,
        ...entConfig,
      };
    },
  },
);
