import { envConfig } from './config';
import { ElectronService, IElectronService } from './electronService';

const ENTERPRISE_DOMAIN_URL = 'ENTERPRISE_DOMAIN_URL'; // 企业地址
const SELECT_ROUTE_PATH = 'SELECT_ROUTE_PATH'; // 最后切换的路由地址

interface IUser {
  [key: string]: any;
}

class App {
  private _user: IUser | null = null;
  private _token: string = '';
  private _electronService?: IElectronService;
  private _isOfficial: boolean = true;
  constructor() {
    window.debug && console.log('init app', this);
  }

  get config() {
    return envConfig[this.env];
  }

  get electronService(): IElectronService | undefined {
    if (!this._electronService) {
      this._electronService = ElectronService();
    }
    return this._electronService;
  }

  get env() {
    return window.MRPBridge?.env ?? 'dev';
  }

  get user() {
    return this._user;
  }

  get token() {
    return this._token;
  }

  get isOfficial() {
    return true;
  }

  set isOfficial(val: boolean) {
    this._isOfficial = val;
  }

  // 初始化整个应用
  initialize() {
    // setConfig(window.env || (isDev ? MockplusEnv.Development : MockplusEnv.Production));
    // 初始化桌面端服务
    this._electronService = ElectronService();
  }
  getOrigin(): Promise<any> | undefined {
    return this.electronService?.getOrigin();
  }
  setCurrentUser(userInfo: IUser) {
    this._user = userInfo;
  }
  setToken(token: string) {
    this.electronService?.setToken(token);
    this._token = token;
  }

  generatorCode() {
    return Date.now().toString(16) + Math.random().toString(16).substring(2);
  }

  setEnterpriseDomain(domain: string) {
    this.setItem(ENTERPRISE_DOMAIN_URL, domain);
    window.MRPBridge?.setWebStore('origin', domain);
  }

  getEnterpriseDomain(): string {
    const domain = this.getItem<string>(ENTERPRISE_DOMAIN_URL);
    return domain ? domain : `${window.MRPBridge?.getWebStore('origin') ?? ''}`;
  }

  setRoutePath(path: string) {
    this.setItem(SELECT_ROUTE_PATH, path);
  }

  getRoutePath(): string | null {
    return this.getItem(SELECT_ROUTE_PATH);
  }

  setItem<T>(key: string, value: T) {
    try {
      localStorage.setItem(key, JSON.stringify(value));
    } catch (e) {
      console.warn(e);
    }
  }

  getItem<T>(key: string): T | null {
    try {
      const str = localStorage.getItem(key);
      return JSON.parse(str as string);
    } catch (e) {
      console.warn(e);
    }
    return null;
  }

  // 官方登录
  login(code: string) {
    const url = this.config.loginUrl.replace(':code', code);
    console.log('login', url);
    window.open(url, '_blank');
  }

  // 企业登录
  enterpriseLogin(domain: string, code: string) {
    this.isOfficial = false;
    window.open(`${domain}/next?pluginCode=${code}&next=dtClient`);
  }

  openRegister() {
    window.open(this.config.signupUrl, '_blank');
  }
}

export default new App();
