/**
 * 桌面端electron API服务适配
 */

enum DesktopChannelType {
  closeWindow = 'window-close',
  createEditorWindow = 'window-create-editor',
  minimizeWindow = 'window-minimize',
  maximizeWindow = 'window-maximize',
  unmaximizeWindow = 'window-unmaximize',
  setWindowPosition = 'window-position',
  setWindowSize = 'window-size',
  showMainWindow = 'window-main-show',
  openWebDevTools = 'web-open-dev-tools',
  login = 'login',
  logout = 'logout',
  reload = 'reload',
  message = 'message',
  createApp = 'create-app',
  loading = 'loading',
  loaded = 'loaded',
  readyToShow = 'ready-to-show',
  getLocalFonts = 'get-local-fonts',
  checkMaximize = 'checkMaximize',
  enterFullScreen = 'enter-full-screen',
  leaveFullScreen = 'leave-full-screen',
}

enum DesktopInvokeChannel {
  getCaptureImage = 'get-capture-image',
  getToken = 'get-token',
  getOrigin = 'get-origin',
  setToken = 'set-token',
  getCheckLoginToken = 'get-check-login-token',
}

enum DesktopEventType {
  undo = 'undo',
  redo = 'redo',
  cut = 'cut',
  copy = 'copy',
  paste = 'paste',
  selectAll = 'select-all',
  maximize = 'maximize',
  unmaximize = 'unmaximize',
  reload = 'reload',
  updateCheck = 'update-check',
  updateProgress = 'update-progress',
  login = 'login',
  logout = 'logout',
  message = 'message',
  createApp = 'create-app',
  targetWeb = 'target-web',
  allWeb = 'all-web',
  getLocalFonts = 'get-local-fonts',
  enterFullScreen = 'enter-full-screen',
  leaveFullScreen = 'leave-full-screen',
  RunPlugin = 'run-plugin',
  PluginUpdated = 'plugin-updated',
  CreatePlugin = 'create-plugin',
  RunLastPlugin = 'run-last-plugin',
  ImportPlugin = 'import-plugin',
  UnreadblePlugins = 'unreadble-plugins',
}
interface IEventEmitter {
  invoke: <T>() => Promise<T>;
  postMessage: () => void;
  send: () => void;
  sendSync: () => () => void;
  sendTo: () => void;
  sendToHost: () => void;
}
interface IIpcRendererEvent {
  ports: number[];
  sender: IEventEmitter;
  senderId: number;
}
interface IIpcRenderer {
  //desc 的类型应该和 data 中的type字段类型一致。
  send: (desc: DesktopChannelType, ...data: any) => void;
  invoke: <D, T>(channel: DesktopInvokeChannel, ...args: T[]) => Promise<D>;
  addListener: <T>(event: DesktopEventType, listener: (event: IIpcRendererEvent, ...arg: T[]) => void) => void;
  removeListener: <T>(event: DesktopEventType, listener: (event: IIpcRendererEvent, ...arg: T[]) => void) => void;
}

interface ILoginParams {
  origin?: string;
  publicPath?: string;
}

export interface IElectronService {
  moveWindow: (x: number, y: number) => void;
  closeWindow: () => void;
  openRegister: () => void; // 打开注册页面
  loginSuccess: (token: string, params?: ILoginParams) => void;
  getToken: () => string;
  getOrigin: () => Promise<string>;
  getIsPrivate: () => Promise<boolean>;
  setToken: (value: string) => void;
  sendTokenStatus: (value: number) => void;
}

