// const config = getConfig(window.env || MockplusEnv.Production);

class RPElectronService implements IElectronService {
  moveWindow(x: number, y: number) {
    window.ipcRenderer?.send(DesktopChannelType.setWindowPosition, x, y);
  }
  closeWindow() {
    window.ipcRenderer?.send(DesktopChannelType.closeWindow);
  }

  openRegister() {
    // window.open(config.signupUrl, '_blank');
  }
  loginSuccess(token: string, params?: ILoginParams) {
    window.ipcRenderer?.send(DesktopChannelType.login, token as never, params);
  }
  getOrigin(): Promise<string> {
    return window.ipcRenderer!.invoke(DesktopInvokeChannel.getOrigin);
  }
  setToken(value: string) {
    window.ipcRenderer?.invoke(DesktopInvokeChannel.setToken, value);
  }

  getToken() {
    return window.desktopToken || '';
  }

  /**
   * 是否为私有部署企业环境
   * @returns
   */
  getIsPrivate(): Promise<boolean> {
    // window.ipcRenderer?.invoke(DesktopInvokeChannel.setToken, value);
    window.desktopPrivate = true;
    return new Promise((resolve) => {
      resolve(true);
    });
  }

  /**
   * 校验token有效性, 0 有效  1 失效
   * @param value
   */
  sendTokenStatus(value: number) {
    window.ipcRenderer?.send(DesktopInvokeChannel.getCheckLoginToken, value);
  }
}

let electronService: RPElectronService | null;
export const ElectronService = () => {
  window.desktopOrigin = '';
  if (window.ipcRenderer) {
    if (!electronService) {
      electronService = new RPElectronService() as RPElectronService;
    }
    return electronService;
  } else if (electronService !== null) {
    electronService = null;
    console.log('未检测到electron环境');
  }
};
