export const isPrivate = navigator.userAgent.indexOf('mockplusrp/ENT') !== -1;

/** 启动软件更新检查 */
export const startupUpdate = (token?: string) => {
    const updateUrl = isPrivate ? `${window.MRPBridge?.getWebStore('origin') ?? '/plugin-downloads/rp-beta/'}/` : 'https://idocapi.mockplus.cn/v1/openapi/file/electron/update/rp-beta/'
    const authToken = token || window.MRPBridge?.getWebStore('token') as string;
    if (authToken) {
        window.MRPBridge?.startUpdater(updateUrl, {}, `Bearer ${authToken}`);
    }
}