import axios from 'axios';
import type { AxiosRequestConfig } from 'axios';

import { PDSetting } from '@/fbs/idoc/models/PDSetting';
import App from './app';
import { isPrivate } from '.';

export interface ResponseResult<T> {
  code: number;
  payload: T;
  message: string;
}

const axiosInstance = axios.create({
  timeout: 1000 * 60 * 5,
  method: 'get',
  responseType: 'json',
});

const userInfo: { [k: string]: any } = {};

// 请求拦截
axiosInstance.interceptors.request.use((request) => {
  const token = window.MRPBridge?.getWebStore('token');
  request.headers['x-mockplus-user-agent'] = 'rpClient';
  request.headers['x-mockplus-app'] = 'rp-for-desktop';
  if (token) {
    request.headers['Authorization'] = `Bearer ${token}`;
  }

  const config = App.config;

  //如果非完整请求地址，就自动补全
  const url = request.url;
  if (!url?.startsWith('http')) {
    let urls = url?.split('/');
    let prefix = urls?.shift() as string;
    if (prefix === 'apicc') {
      urls?.unshift(config.apiCC ?? '');
    } else if (prefix === 'apirp') {
      urls?.unshift(config.apiRP ?? '');
    } else {
      console.error('this api prefix not exist:' + prefix);
    }
    request.url = urls?.join('/');
  }

  // 通过配置设置，是否使用代理方式 - 通常用于私有部署 （目前：建行在用）
  const OriginMethod = request.headers['OriginMethod'];
  if (OriginMethod && OriginMethod !== 'false') {
    request.headers['OriginMethod'] = request.method;
    let method = request.method;
    if (method && /put|patch/.test(method)) {
      method = 'post';
    }
    if (method && /delete|head|options/.test(method)) {
      method = 'get';
    }
    request.method = method;
  } else {
    delete request.headers['OriginMethod'];
  }

  return request;
});

// 前端API请求
export const request = async <T = any>(options: AxiosRequestConfig, useProxy: boolean = true) => {
  // 私有部署可能需要更改请求行为参数 - 目前建行在用
  const originMethod = axiosInstance.defaults.headers['OriginMethod'];
  if (isPrivate && useProxy && !originMethod) {
    try {
      await getPDConfigs();
    } catch (error) {
      axiosInstance.defaults.headers['OriginMethod'] = 'false';
    }
  }
  const res = await axiosInstance.request<ResponseResult<T>>({
    method: 'get',
    ...options,
  });
  return res.data;
};

/**
 * 检验token是否有效，无效的话需要重新登录
 * @returns
 */
export const checkToken = async () => {
  return request({
    url: 'apicc/user/info',
    timeout: 1000 * 30,
  }).then((data) => {
    try {
      Object.assign(userInfo, data.payload);
    } catch (error) {
      console.log(error);
    }
    return data;
  });
};

/**
 * 获取私有部署是否需要代理请求 - 建行需要
 * @returns
 */
export async function getPDConfigs() {
  return await axiosInstance
    .request<ResponseResult<PDSetting>>({
      method: 'get',
      url: 'apicc/pdConfig',
    })
    .then(({ data }) => {
      if (data.payload?.methodProxy) {
        axiosInstance.defaults.headers['OriginMethod'] = 'get';
      } else {
        axiosInstance.defaults.headers['OriginMethod'] = 'false';
      }
      return data;
    });
}

